import Modal from "AC/modules/modal";
import { initAcServices } from "AC/helpers/admin-columns";
class Feedback {
    constructor(form) {
        this.form = form;
        this.initEvents();
    }
    initEvents() {
        let self = this;
        let $form = jQuery(this.form);
        $form.submit((e) => {
            e.preventDefault();
        });
        $form.find('[name=frm_ac_fb_submit]').on('click', function () {
            self._sendData().done((response) => {
                if (response.success) {
                    $form.find('.ac-modal__dialog__footer').remove();
                    $form.find('.ac-modal__dialog__content').html(response.data);
                }
                else {
                    $form.find('.ac-feedback__error').html(response.data);
                }
            });
        });
    }
    _sendData() {
        let $form = jQuery(this.form);
        return jQuery.ajax({
            url: ajaxurl,
            method: 'post',
            data: {
                action: 'acp-send-feedback',
                email: $form.find('[name=name]').val(),
                feedback: $form.find('[name=feedback]').val(),
                _ajax_nonce: $form.find('[name=_ajax_nonce]').val()
            }
        });
    }
}
document.addEventListener("DOMContentLoaded", () => {
    initAcServices().getService('Modals').register(new Modal(document.getElementById('ac-modal-feedback')), 'feedback');
    new Feedback(document.querySelector('#frm-ac-feedback'));
});
